body {
    margin: 0;
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.MainContainer {
    max-width: 500px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    flex-shrink: 1;
}

@media only screen and (max-width: 600px) and (min-width: 350px) {
    .MainContainer {
        max-width: 300px;
    }
}

@media only screen and (max-width: 350px) {
    .MainContainer {
        max-width: 200px;
    }
}

.Portfolio {
    width: 100%;
    display: flex;
    justify-content: center;
}

.Links {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    font-size: 1.15rem;
    gap: 5px;
}

.Photo {
    flex-grow: 1;
    flex-shrink: 1;
    padding-bottom: 20px;
}

.TopText {
    flex-grow: 1;
    flex-shrink: 1;
}

.Title {
    font-size: 3rem;
    color: #551ba6;
}

.Subtitle {
    padding-top: 10px;
    font-size: 1.75rem;
    color: #551ba6;
}

.AboutText {
    font-size: 1.25rem;
    padding-bottom: 10px;
}
.ProjectText {
    font-size: 1.25rem;
}
.Link {
    color: #551ba6;
    text-decoration: none;
}
.Link:hover {
    text-decoration: underline;
    cursor: pointer;
}
.List {
    color: #551ba6;
}
